import { Component, OnInit, Output } from '@angular/core';
import {ActivatedRoute} from '@angular/router';
import { Producto } from '../producto';
import { ProductoService } from '../producto.service'
import { CarritoService } from  '../carrito.service';

@Component({
    selector: 'app-productos',
    templateUrl: './productos.component.html',
    styleUrls: ['./productos.component.css']
})
export class ProductosComponent 
{
    @Output() slctProducto: Producto;
    productos: Producto[];
    categoria: number;
    
    constructor(private prodServ:ProductoService, private carrService: CarritoService, private route:ActivatedRoute){}
    
    ngOnInit(){
        this.prodServ.getProductos().then(respuesta => this.productos = respuesta);
        this.categoria = +this.route.snapshot.params['categoria'];
    }

    selecProducto(producto: Producto){
        this.slctProducto = producto;
    }

    agregarCarrito(p: Producto):void{
        //this.carrito.push(p.id);
        this.carrService.addElemento(p.id);
        //alert(this.carrito.length);
    }
}


